.banner-top{
    padding: 40px;
    position: relative;
    margin-top: 80px;   
}
.banner-top:before {
    content: "";
    background-image: url(https://demo.dezven.com/project/web-design/portfolio/1/images/dots.png);
    position: absolute;
    top: 0;
    left: -50px;
    right: 0;
    bottom: 0;
    background-position: left;
    background-size: cover;
    animation-name: rotate;
    animation-duration: 80s;
    animation-iteration-count: infinite;
}
@keyframes rotate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}
.banner-top:after {
    content: "";
    position: absolute;
    bottom: 0;
    top: 0;
    left: 0;
    width: 115%;
    border: 20px solid #ea4020;
    border-right: 0px;
    border-radius: 47% 0% 0% 47%;
    background: #fff;
}

.banner-img {
    border-radius: 50% 50% 0% 50%;
    border: 15px solid #292929 !important;
    position: relative;
    z-index: 1;
    overflow: hidden;
}
.banner-img img{
    width: 100%;
}

*, ::after, ::before {
    box-sizing: border-box;
}

.banner-left h1{
    font-weight: 900;
}

@media only screen  and (max-width:768px) {
    .banner-left h4{
        margin-top: 0px;
    }
    .banner-top:after{
        width: 100%;
    }
}