.heading {
    text-align: center;
    width: 100%;
    float: left;
    padding-bottom: 20px;
    margin-top: 0px;
  }
  .heading h5 {
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #292929;
    font-size: 7vw;
    color: transparent;
    font-weight: 600;
    letter-spacing: 0.8rem;
    opacity: 0.05;
    pointer-events: none;
    margin: 0px;
    padding: 0px;
  }
  .contact-info {
    width: 100%;
    float: left;
    background-color: #fff;
    padding: 10px 30px 10px 30px;
    border-radius: 8px;
    border: 1px solid #e2e8f0;
    box-shadow: 0 0.375rem 1.5rem 0 rgba(140, 152, 164, 0.125);
    margin-bottom: 20px;
}
.contact-left h3 {
    border-left: 2px solid #ea4020;
    padding-left: 10px;
    color: #565656;
    font-size: 20px;
    font-weight: 600;
    width: 100%;
    float: left;
    margin-top: 20px;
}
 
.contact-left a {
    padding-left: 10px;
    margin-bottom: 7px;
    width: 100%;
    float: left;
    text-decoration: none;
    color: black;
}
.contact-form {
    border: 1px solid #e2e8f0;
    padding: 30px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 0.375rem 1.5rem 0 rgba(140, 152, 164, 0.125);
}
.contact-form h3 {
    border-left: 2px solid #ea4020;
    padding-left: 10px;
}
.footer-section {
    padding-top: 25px;
    padding-bottom: 15px;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.1);
}