.service {
  width: 350px;
  border: 1px solid #e2e8f0;
  border-radius: 7px;
  box-shadow: 0 0.375rem 1.5rem 0 rgba(140, 152, 164, 0.125);
  margin-top: 88px;
  padding-bottom: 30px;
}
.service-hex {
  margin-top: 30px;
  width: 104px;
  height: 60px;
  background-color: #ea4020;
  position: relative;
  top: -55px;
  left: 40%;
}
.service-hex:before {
  content: " ";
  width: 0;
  height: 0;
  border-bottom: 30px solid #ea4020;
  border-left: 52px solid transparent;
  border-right: 52px solid transparent;
  position: absolute;
  top: -32px;
  left: -1px;
}
.service-hex:after {
  content: "";
  width: 0;
  position: absolute;
  bottom: -32px;
  border-top: 30px solid #ea4020;
  border-left: 52px solid transparent;
  border-right: 52px solid transparent;
  left: -1px;
}
.service-inner {
  text-align: center;
  padding: 5px 44px;
  font-size: 17px;
}